.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%); }

.page {
  height: 100%;
  margin: 0 auto; }

html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  min-width: 100%;
  height: 100%;
  margin: 0 auto;
  color: #102654;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  font-family: "PT Mono", "Arial", sans-serif;
  background-color: white;
  position: relative; }

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative; }

.wrapper__overlay::after {
  content: "";
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  position: absolute;
  z-index: 75;
  overflow: hidden; }

main {
  flex: 1 1 auto; }

h1,
h2,
h3,
ul {
  padding: 0;
  margin: 0; }

li {
  list-style: none;
  padding: 0;
  margin: 0; }

a {
  text-decoration: none;
  color: #102654; }

.header {
  width: 100%;
  min-width: 320px;
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto; }

.header__wrapper {
  min-width: 320px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  gap: 10px 20px;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin: 0 auto; }
  @media (min-width: 1200px) {
    .header__wrapper {
      max-width: 1200px;
      padding: 28px 23px 30px 18px; } }
  @media (max-width: 1199px) {
    .header__wrapper {
      max-width: 1199px;
      padding: 39px 45px 33px 43px; } }
  @media (max-width: 767px) {
    .header__wrapper {
      max-width: 767px;
      padding: 19px 16px 16px 14px; } }

.header__wrapper svg {
  cursor: initial; }
  @media (min-width: 1200px) {
    .header__wrapper svg {
      width: 89px;
      height: 20px; } }
  @media (max-width: 1199px) {
    .header__wrapper svg {
      width: 82px;
      height: 17px; } }
  @media (max-width: 767px) {
    .header__wrapper svg {
      width: 68px;
      height: 15px; } }

.header__wrapper div {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: wrap; }

.header__wrapper a {
  font-family: "Bebas Neue";
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.02em;
  color: white;
  margin: 0; }
  @media (max-width: 1199px) {
    .header__wrapper a {
      font-size: 24px;
      line-height: 24px; } }
  @media (max-width: 767px) {
    .header__wrapper a {
      font-size: 20px;
      line-height: 20px; } }

.header__wrapper div a:hover,
.header__wrapper div a:focus,
.header__wrapper div a:active {
  border-bottom: 2px solid white; }
